/* eslint-disable no-nested-ternary */
import { memo, useCallback, useEffect, useRef } from 'react';
import { I_Scene } from '@containers/Home/types';
import { DEFAULT_VALUE_MAP_CONFIG } from '@containers/Home/constants';
import dynamic from 'next/dynamic';
import { useTourEventState } from '@containers/Home/global/TourStats';
import { useEventMap } from '@containers/Home/hook/useEventMap';
import { useSelector } from 'react-redux';
import {
  selectLanguageProject,
  selectThemeConfigGlobal,
} from '@containers/Home/selectors';
import { Tooltip } from 'antd';
import { ActionMapWrapper, MapBoxWrapper, SIZE_BOX } from './styled';
import GoogleMap, { Type_RefFuncGoogleMap } from './GoogleMap';
import { isServer } from '@utils/helper';

const Popup = dynamic(() => import('./Popup'));

interface Props {
  onChangeScene: (scene: I_Scene) => void;
}

const MapBox = memo(({ onChangeScene }: Props) => {
  const LANGUAGES = isServer ? {} : window.languages || {};

  const size = useRef({ width: 0, height: 0 });
  const containerRef = useRef<HTMLDivElement>(null!);
  const googleMapRef = useRef<Type_RefFuncGoogleMap>(null!);

  const themeConfig = useSelector(selectThemeConfigGlobal);
  const languageProject = useSelector(selectLanguageProject);
  const [tourEventStats] = useTourEventState('state');

  const {
    mapData,
    configMap,
    visiblePopup,
    sceneSelected,
    markerSelected,
    configDisplayBox,
    onCloseMap,
    onOpenPopup,
    onClosePopup,
  } = useEventMap();

  const onCoverAllMarker = useCallback(() => {
    googleMapRef.current?.onFitBounds();
  }, []);

  const onCenterToMarkerActive = useCallback(() => {
    googleMapRef.current?.onPanToMarker(markerSelected);
  }, [markerSelected]);

  const handleResize = useCallback(
    entries => {
      for (const entry of entries)
        size.current = {
          width: Math.round(entry.contentRect.width),
          height: Math.round(entry.contentRect.height),
        };

      if (
        size.current.width === SIZE_BOX.normal.width - 4 ||
        size.current.width === SIZE_BOX.scale.width - 4
      ) {
        setTimeout(() => {
          googleMapRef.current?.onInvalidateSize();
          googleMapRef.current?.onPanToMarker(markerSelected);
        }, 500);
      }
    },
    [markerSelected],
  );

  useEffect(() => {
    const resizeObserver = new ResizeObserver(handleResize);
    if (containerRef.current) resizeObserver.observe(containerRef.current);
    return () => {
      resizeObserver.disconnect();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [visiblePopup]);

  return !visiblePopup ? (
    <MapBoxWrapper
      ref={containerRef}
      bgColor={configMap?.bgColor || DEFAULT_VALUE_MAP_CONFIG.bgColor || '#000'}
      position={configDisplayBox?.position || 'bottomRight'}
      offsetTop={configDisplayBox?.offsetTop || 10}
      offsetBottom={configDisplayBox?.offsetBottom || 10}
      offsetRight={configDisplayBox?.offsetRight || 10}
      offsetLeft={configDisplayBox?.offsetLeft || 10}
      className={`${tourEventStats.isVisibleMap ? '' : 'hide'} ${
        themeConfig?.selected === 'basekrpano'
          ? 'basekrpano'
          : themeConfig?.selected === 'custom1'
          ? 'custom1'
          : themeConfig?.selected === 'wall'
          ? 'wall'
          : 'default'
      }`}
    >
      <ActionMapWrapper>
        <div className="--left" />
        <div className="--right">
          <Tooltip
            title={LANGUAGES[languageProject]?.actionMarkerCenter}
            placement="top"
          >
            <div className="action--item" onClick={onCenterToMarkerActive}>
              <i aria-hidden className="far fa-crosshairs" />
            </div>
          </Tooltip>
          <Tooltip
            title={LANGUAGES[languageProject]?.actionMarkerCover}
            placement="top"
          >
            <div className="action--item" onClick={onCoverAllMarker}>
              <i aria-hidden className="far fa-ball-pile" />
            </div>
          </Tooltip>
          <Tooltip
            title={LANGUAGES[languageProject]?.actionMarkerExpand}
            placement="top"
          >
            <div className="action--item" onClick={onOpenPopup}>
              <i aria-hidden className="far fa-expand" />
            </div>
          </Tooltip>
          <Tooltip
            title={LANGUAGES[languageProject]?.actionMarkerClose}
            placement="top"
          >
            <div className="action--item" onClick={onCloseMap}>
              <i aria-hidden className="far fa-times" />
            </div>
          </Tooltip>
        </div>
      </ActionMapWrapper>

      <GoogleMap
        ref={googleMapRef}
        mapData={mapData}
        sceneSelected={sceneSelected}
        markerSelected={markerSelected}
        configMap={configMap}
        onChangeScene={onChangeScene}
      />
    </MapBoxWrapper>
  ) : (
    <Popup
      visible={visiblePopup}
      markerSelected={markerSelected}
      mapData={mapData}
      configMap={configMap}
      onChangeScene={onChangeScene}
      onCancel={onClosePopup}
    />
  );
});

export default MapBox;
